import React from 'react'

//import the SVGs
import SLetter  from '../icons/sLetter'
import MLetter  from '../icons/mLetter'

const IconLayout = (props) => {

    let logos = []
    const bodyWidth = document.body.clientWidth
    // const bodyHeight =  document.body.clientHeight
    const bodyHeight = 900
    console.log(bodyHeight)

    for (let i = 0; i < 30; i++) {
        const randPosX = Math.floor((Math.random()*bodyWidth));
        const randPosY = Math.floor((Math.random()*bodyHeight));
        if( (i % 2) == 0) {
            logos.push(
                < SLetter key={i} styles={{position: 'absolute', top: `${randPosY}px`, left: `${randPosX}px`}} />
            )
        }else {
            logos.push(
            < MLetter key={i} styles={{position: 'absolute', top: `${randPosY}px`, left: `${randPosX}px`}} />
            )
        }
    }

    return(
        <div className="logo-container">
            {logos}
        </div>
    )
}

export default IconLayout